import React from 'react';
import '../Profile/Profile.css';

function FutureCard({ closeProfile, icons, boxData }) {


    new Date().toLocaleTimeString()
    var options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    var date = new Date(boxData.dt * 1000).toLocaleTimeString('en-us', options).replace(/,/g, "").split(' ');

    return (
        <div className='forecast-popup'>
            <p className='close' style={{ color: 'black', marginRight: '15px', marginTop: '5px' }}></p>
            <div id='container' className=''>

                <div className='column-6'>
                    <br></br>
                    <h2>Forecast</h2>
                    <img className='foreBoxImg' src={icons[boxData.weather[0].icon + '.png']} alt=''></img>
                    <p className='temperature foreBox-temp'> <mark className='temp' style={{ color: 'black' }}>{Math.round(boxData.main.temp)}</mark><mark className='celcius' style={{ color: 'black' }}>c</mark> </p>
                    <p className='D-M-Y foreboxColor'><b>{date[0]}</b></p><p>{date[2]}, {date[1]} {date[3]}</p>
                    <p className='foreboxColor'>IN</p>
                    <br></br>
                    <h5 className='description foreboxColor'>{boxData.weather[0].description}</h5>
                </div>

                <div className='column-7'>
                    <br></br><br></br>
                    <div id='container' className=''>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['humidity.png']} alt='' className='mini-image'></img> Humidity</p>
                                <p className='mini-value'>{boxData.main.humidity} %</p>
                                <p className='mini-add'>East</p>
                            </div>
                        </div>
                        <br></br>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['feelsLike.png']} alt='' className='mini-image'></img> Feels Like</p>
                                <p className='mini-value'>{Math.round(boxData.main.feels_like)} %</p>
                                <p className='mini-add'></p>
                            </div>
                        </div>
                        <br></br>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['pressure.png']} alt='' className='mini-image'></img> Pressure</p>
                                <p className='mini-value'>{boxData.main.pressure}</p>
                                <p className='mini-add'>hpa</p>
                            </div>
                        </div>
                    </div>

                    <br></br><br></br>
                    <div id='container' className=''>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['windDegree.png']} alt='' className='mini-image'></img> Wind Degree</p>
                                <p className='mini-value'>{boxData.wind.deg} N  </p>
                                <p className='mini-add'></p>
                            </div>
                        </div>
                        <br></br>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['windSpeed.png']} alt='' className='mini-image'></img> Wind Speed</p>
                                <p className='mini-value'>{boxData.wind.speed} m/s</p>
                                <p className='mini-add'></p>
                            </div>
                        </div>
                        <br></br>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['pm10.jpg']} alt='' className='mini-image'></img> Wind Gust</p>
                                <p className='mini-value'>{Math.round(boxData.wind.gust)} km/h</p>
                                <p className='mini-add'>visible</p>
                            </div>
                        </div>
                    </div>

                    <br></br><br></br>
                    <div id='container' className=''>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['dew.png']} alt='' className='mini-image'></img> Grnd Level</p>
                                <p className='mini-value'>{boxData.main.grnd_level} c</p>
                                <p className='mini-add'>mm</p>
                            </div>
                        </div>
                        <br></br>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['preci.png']} alt='' className='mini-image'></img> PoP</p>
                                <p className='mini-value'>{boxData.pop} %</p>
                                <p className='mini-add'>Precipitation</p>
                            </div>
                        </div>
                        <br></br>
                        <div className='column-9'>
                            <div className='card'>
                                <p className='mini-desc'><img src={icons['uvi.png']} alt='' className='mini-image'></img> Sea Level</p>
                                <p className='mini-value'>{boxData.main.sea_level} nm</p>
                                <p className='mini-add'>depth</p>
                            </div>
                        </div>
                        <br></br>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FutureCard